.backdrop-franchise {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  opacity: 1;
  transition: opacity 200ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop-franchise.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal--franchise {
  .backdrop {
    position: fixed;
    z-index: 50;
    top: 0;
    left: 0;
    overflow-y: scroll;

    width: 100%;
    height: 100%;
    background: transparent;

    transition: opacity 255ms cubic-bezier(0.25, 0.46, 0.45, 0.94),
      visibility 255ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
}
