    top: 9px;
    left: 488px;
  }

  .header-button {
    cursor: pointer;
    width: 152px;
    height: 42px;
    background-color: var(--main-yellow);
    transition: background-color 250ms var(--cubic-bezier);
    border: 0;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 0;
    display: flex;
    position: absolute;
    bottom: 0;
    right: 0;
