.advantages-section {
  max-width: 100%;
  padding-top: 60px;
  background-color: var(--main-green);
}
.label-hero {
  display: none;
}
.korova-fone {
  padding-top: 22px;

  background: linear-gradient(var(--main-yellow), var(--main-yellow)),
    var(--gradient); // box-shadow: 0px 4.48583px 16.8218px var(--text-dark);
  border-radius: 50%;
  justify-content: center;
}
.description-conteiner {
  position: relative;

  width: 220px;
  padding-top: 40px;

  margin-left: 50px;
  margin-right: 50px;
}
.button-read {
  width: 164px;
  height: 42px;
  display: flex;

  margin-top: 12px;
  padding-left: 34px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.28;
  align-items: center;
  text-align: center;

  color: var(--text-dark);
  cursor: pointer;

  background: var(--main-yellow);
  border-radius: 10px;
  border-color: var(--main-yellow);
  transition: background-color 250ms var(--cubic-bezier);
}

.button-read-svg {
  display: flex;
  align-items: center;
  width: 6px;
  height: 10px;
  background-color: azure;
  margin-left: 8px;
}

.creaw-box {
  text-align: center;
  width: 220px;
  margin-left: 45px;
  margin-right: 45px;
  margin-top: 50px;
}
.advantages-text-mass {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 170%;

  text-align: center;

  color: var(--text-light);
}

